.item{height:80px;display:flex;justify-content:space-between;align-items:center;margin-bottom:40px}.item .info{display:flex;max-width:350px;width:100%}.item .info .infoImageContainer{max-width:80px;width:100%;max-height:80px;margin-right:15px}.item .info .infoImageContainer .infoImage{width:100%;height:100%;object-fit:contain}.item .info .infoText{display:flex;flex-direction:column;justify-content:center}.item .info .infoText .infoTitle{font-size:22px;font-weight:700;margin-bottom:2px}.item .info .infoText .infoParameters{color:#8d8d8d;font-size:18px;font-weight:400}.item .price{font-family:"Proxima-Nova";font-size:22px}.item .price span{font-size:18px;font-weight:400}.item .delete{width:30px;height:30px;border:2px solid #d7d7d7;border-radius:50%;position:relative;cursor:pointer;transition:all .3s}.item .delete:before{font-family:"IcoFont";content:"";position:absolute;top:4px;left:4px;font-size:18px;color:#d7d7d7}.item .delete:hover{border:2px solid #000;background-color:#000}.item .delete:hover:before{color:#fff}.item .delete:active{transform:translateY(5px)}@media(max-width: 700px){.item{flex-wrap:wrap;height:auto}.item:last-child{margin-bottom:70px}.item .info{max-width:100%;margin-bottom:10px}}