.header{max-width:1340px;width:100%;height:100px;background-color:#fff;border-radius:13px;display:flex;justify-content:space-between;align-items:center;padding:0 15px;margin-bottom:30px}.header .logo{display:flex}.header .logo .logoImage{margin-right:15px;object-fit:contain}.header .logo .logoText .logoTitle{font-family:"Proxima-Nova";font-weight:700;font-size:25px;letter-spacing:.4px;line-height:20px;margin-bottom:6px;-webkit-text-stroke-color:#c7b299;-webkit-text-stroke-width:.4px;color:#000}.header .logo .logoText .logoSubtitle{color:#7b7b7b;font-size:16px;letter-spacing:.1px}.header .cart{background-color:#fe5f1e;border-radius:50px;position:relative;transition:all .3s}.header .cart:hover{background-color:#e3541b}.header .cart:active{transform:translateY(-5px)}.header .cart .cartIcon{position:absolute;top:12px;left:13px;cursor:pointer;stroke:#fff}.header .cart .cartNumber{display:inline-block;padding:13px 12px 12px 40px;color:#fff;font-size:20px;font-family:"Proxima-Nova";font-weight:700}@media(max-width: 550px){.header{height:70px;padding:0 10px}.header .login{max-width:45px;height:45px;padding:0;color:transparent;position:relative}.header .login:hover{color:transparent}.header .login:active{transform:translateY(5px)}.header .login:before{font-family:"IcoFont";content:"";color:#525151;position:absolute;left:10px;top:8px;font-size:25px}.header .logo{display:flex}.header .logo .logoImages{margin-right:15px;object-fit:contain}.header .logo .logoText{max-width:70px}.header .logo .logoText .logoTitle{font-family:"Proxima-Nova";font-weight:700;font-size:22px;letter-spacing:.4px;line-height:20px;margin-bottom:6px;-webkit-text-stroke-color:#c7b299;-webkit-text-stroke-width:.4px;color:#000;line-height:24px;margin-top:5px}.header .logo .logoText .logoSubtitle{display:none}}