.form{flex-direction:column;align-items:left;display:none}.form._active{display:flex}.form .container{max-width:100%;width:100%;display:inline-block;margin-bottom:5px;font-size:18px;font-weight:700;letter-spacing:.3px;margin-bottom:20px}.form .container .field{max-width:100%;width:100%;border:1px solid #ccc;padding:10px 15px;border-radius:5px;font-size:20px;margin-top:5px}.form .container .error{font-weight:400;color:#f25c5c;position:relative;font-size:18px;padding-left:25px;margin:7px 0 5px 0;display:none}.form .container .error._active{display:block}.form .container .error:before{font-family:"IcoFont";content:"";position:absolute;font-size:20px;color:#f25c5c;top:-1px;left:0px;transition-duration:.3s}.form .submit{max-width:100%;width:100%;height:45px;font-size:20px;border-radius:5px;background-color:#f0f0f0;letter-spacing:.5px;-webkit-text-stroke-color:#9e9e9e;-webkit-text-stroke-width:.3px;transition-duration:.3s;position:relative}.form .submit:hover{background-color:#ccc;color:#fff;-webkit-text-stroke-color:#fff}.form .submit:active{transform:scale(0.9, 0.9)}.form .submit._auth{color:#f0f0f0;-webkit-text-stroke-color:#f0f0f0;pointer-events:none}.form .submit._auth:before{font-family:"IcoFont";content:"";position:absolute;top:9px;left:calc(50% - 8px);font-size:25px;color:#2d2d2d}