.counter{display:flex;height:32px}.counter .minus{width:30px;height:30px;border:2px solid #fe5f1e;border-radius:50%;position:relative;cursor:pointer;transition:all .3s}.counter .minus:before{font-family:"IcoFont";content:"";position:absolute;top:4px;left:4.5px;font-size:18px;color:#fe5f1e}.counter .minus:hover{background-color:#fe5f1e}.counter .minus:hover:before{color:#fff}.counter .minus:active{transform:translateY(5px)}.counter .total{pointer-events:none}.counter .total .totalResult{font-size:20px;padding:0 5px;margin:5px 10px 0 10px;font-weight:700}.counter .plus{width:30px;height:30px;border:2px solid #fe5f1e;border-radius:50%;position:relative;cursor:pointer;transition:all .3s}.counter .plus:before{font-family:"IcoFont";content:"";position:absolute;top:7px;left:7px;font-size:13px;color:#fe5f1e}.counter .plus:hover{background-color:#fe5f1e}.counter .plus:hover:before{color:#fff}.counter .plus:active{transform:translateY(-5px)}