.wrapper {
   width: 100%;
   min-height: 100vh;
   overflow: hidden;
   display: flex;
   flex-direction: column;
   align-items: center;
   background-color: #ffdf8c;
   padding: 30px 10px 40px 10px;
}

@media (max-width: 550px) {
   .wrapper {
      padding: 15px 10px 15px 10px;
   }
}
